'use client'

import { useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'

export default function Error({
  error,
  reset,
}: {
  error: Error & { digest?: string }
  reset: () => void
}) {
  useEffect(() => {
    // Log the error to an error reporting service
    console.error(error)
  }, [error])

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center py-8">
      <div className="max-w-md mx-auto px-4 sm:px-6 lg:px-8">
        <Card className="text-center">
          <CardHeader>
            <div className="text-6xl mb-4">⚠️</div>
            <CardTitle className="text-2xl text-red-600">
              Oops! Terjadi Kesalahan
            </CardTitle>
            <CardDescription className="text-lg">
              Maaf, terjadi kesalahan yang tidak terduga. Tim kami telah diberitahu tentang masalah ini.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="bg-gray-50 p-4 rounded-lg text-left">
              <p className="text-sm text-gray-600 font-mono">
                Error: {error.message || 'Unknown error occurred'}
              </p>
              {error.digest && (
                <p className="text-xs text-gray-500 mt-2">
                  ID: {error.digest}
                </p>
              )}
            </div>
            
            <div className="flex flex-col sm:flex-row gap-3 justify-center">
              <Button onClick={reset}>
                Coba Lagi
              </Button>
              <Button variant="outline" asChild>
                <a href="/">Kembali ke Beranda</a>
              </Button>
            </div>
            
            <div className="text-sm text-gray-500 mt-4">
              <p>Jika masalah terus berlanjut, silakan hubungi kami di:</p>
              <a 
                href="mailto:support@monetasikarya.com" 
                className="text-blue-600 hover:text-blue-800"
              >
                support@monetasikarya.com
              </a>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
